ервая — время согласования всех деталей договора купли-продажи между клиентом и продавцом. Вторая — время согласования недвижимости в Архитектурном и Земельном комитетах Таиланда. ")]),_vm._v(" "),_c('h5',[_vm._v("В целом же процедура состоит из таких этапов: ")]),_vm._v(" "),_vm._m(1),_vm._v(" "),_c('p',[_vm._v(" Из документов от покупателя требуется только заграничный паспорт и его заверенная копия. При покупке вторичного жилья необходима также справка ТТ3 из тайского банка — она подтверждает, что перевод денег поступил из-за рубежа в иностранной валюте и был легально конвертирован в тайские баты. Можно также перевезти деньги наличными, указав на границе их предназначение.  ")])])]),_vm._v(" "),_c('li',{staticClass:"info__list-item",class:{'is-active': _vm.acordionItem11},on:{"click":function($event){_vm.acordionItem11 = !_vm.acordionItem11}}},[_c('h4',{staticClass:"info__list-title js-toggle"},[_vm._v(" Каким налогом будет облагаться недвижимость после покупки? "),_c('svg',{staticClass:"icon icon-arrd"},[_c('use',{attrs:{"xlink:href":"../../static/sprite.svg#icon-arrd"}})])]),_vm._v(" "),_c('div',{staticClass:"info__list-content",class:{'activate-item' : _vm.acordionItem11}},[_vm._v("\n                  В Таиланде нет регулярного налога на недвижимость. Если вы владеете квартирой, нужно платить только за содержание комплекса, сумма зависит от конкретного владельца кондоминиума.\n\n\nНалог на землю в Таиланде составляет несколько десятков батов на за 1 рай (1 600 м²). Это такие незначительные суммы, что счёт выставляется раз в несколько лет. \n\n\nЕсли вы сдаёте недвижимость в аренду, нужно выплачивать налог в 12,5% от оценочной арендной стоимости объекта.\n                ")])]),_vm._v(" "),_c('li',{staticClass:"info__list-item",class:{'is-active': _vm.acordionItem12},on:{"click":function($event){_vm.acordionItem12 = !_vm.acordionItem12}}},[_c('h4',{staticClass:"info__list-title js-toggle"},[_vm._v(" Каковы перспективы возврата инвестиций при покупке недвижимости